import React from "react";
import { connect } from 'react-redux'
import { Link } from "react-router-dom";

import {
    Button,
    Card,
    CardBody,
    CardImg,
    CardTitle,
    CardText,
    Row,
    Col,
    Container
} from "reactstrap";

import AlternativeHeader from "components/Headers/AlternativeHeader.jsx";
import { getVenues } from '../../../redux/actions/venuesActions';
import "./Venues.scss"

class Venues extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pagination: {
                limit: 1000,
                offset: 0,
                selected: 0,
                search: '',
                ordering: 'createdAt',
                desc: false
            }
        }
    }
    componentDidMount() {
        this.props.getVenues(this.state.pagination);
    }

    getFirstSpaceId(roomMapMapping) {
      return JSON.parse(roomMapMapping).length > 0 && JSON.parse(roomMapMapping)[0].id;
    }

    render() {
        const { data } = this.props;
        const { result } = data
        return <>
            <AlternativeHeader title="Venues" />
            <Container className="mt--6 venues" fluid>
                <Row>
                    {result && result.map(item => <Col lg={3} md={4} key={item.id}>
                        <Card>
                            <CardImg alt="..." src={item.image} top />
                            <CardBody>
                                <CardTitle>{item.name}</CardTitle>
                                <CardText>
                                    Spaces: {item.spacesCount}
                                </CardText>
                                <div>
                                    <Link to={`/admin/venues/${item.id}`}><Button color="default" type="button" className="mr-2 mb-2">Venue Details</Button></Link>
                                    {item.spacesCount > 0 && <Link to={`/admin/venues/${item.id}/spaces/${this.getFirstSpaceId(item.roomMapMapping)}`}><Button color="default" type="button" className="mb-2">Spaces</Button></Link>}
                                </div>
                            </CardBody>
                        </Card>
                    </Col>)}
                    <Col lg={3} md={4} className="d-flex align-items-center justify-content-center">
                        <Link to={`/admin/venues/create`} className="add-new"><i className="ni ni-fat-add" /></Link>
                    </Col>
                </Row>
            </Container>
        </>
    }
}

const mapStateToProps = state => {
    return {
        data: state.venues.data,
        isLoading: state.venues.isLoading,
    };
}

const mapActionToProps = { getVenues };

export default connect(mapStateToProps, mapActionToProps)(Venues);
